.container{
    cursor: pointer;
    
    .element{
        background-color: var(--clr-dark-transparent);
        margin-bottom: 0.5rem;
        padding: 0.5rem;
        border: 2px solid transparent;

        display: flex;
        align-items: center;
        justify-content: space-between;

        div{
            display: flex;
            align-items: center;
        }

        img{
            width: 50px;
            height: 50px;
            border-radius: 5px;
            margin-right: 0.5rem;
            background-color: white;
        }

        button{
            border-radius: 5px;
            background-color: var(--clr-light-transparent);
            padding: 0.5rem;
            display: flex;
            &:hover{
                background-color: var(--clr-red);
                svg{
                    color: white ;
                }
            }
        }
    }
    .selected{
        border: 2px solid var(--clr-blue);
    }

}